@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "market_decoregular";
  src: url(/build/static/media/market_deco-webfont.d55dffe2.woff2) format("woff2"),
    url(/build/static/media/market_deco-webfont.f40c2500.woff) format("woff"),
    url(/build/static/media/market_deco.b40a88ee.ttf) format("ttf");
  font-weight: normal;
  font-style: normal;
}

/* display for mobile and tablet */
@media (max-aspect-ratio: 4/5) and (orientation: portrait) {
  html {
    font-size: 14px;
  }

  body .enter img {
    height: auto;
    width: 90%;
  }

  body .zizi-sidebar {
    left: 0;
    right: 0;
    top: auto;
    bottom: 0;
    height: 30rem;
    flex-direction: column-reverse;
    transition: bottom 1s ease;
  }

  body .zizi-sidebar .main-sidebar {
    width: 100%;
  }

  body .zizi-sidebar.closed {
    left: 0;
    bottom: -26rem;
  }
  
  body .zizi-sidebar .mini-sidebar {
    flex-direction: row;
  }

  body .zizi-sidebar .mini-sidebar .top-buttons, body .zizi-sidebar .mini-sidebar .centered-buttons {
    flex-direction: row;
    display: inline;
  }

  body .main-bar-content {
    flex-shrink: 0;
    margin-top: 2rem;
  }

  body .zizi-sidebar .mini-sidebar .centered-buttons > a {
    padding: 0 20px 0 20px;
  }

  body .close-sidebar-left {
    height: 15px;
    width: 100%;
  }

  body .close-sidebar-left .close-button2 {
    display: inherit;
    padding-left: 15px;
    padding-top: 30px;
  }

  body .secondary-sidebar {
    left: 0;
    right: 0;
    top: auto;
    bottom: 0;
    height: 30rem;
    flex-direction: column;
    width: 100%;
    transition: bottom 1s ease;
  }

  body .secondary-sidebar .content-sidebar {
    padding-top: 0;
  }

  body .secondary-sidebar.closed {
    bottom: -31rem;
  }

  body .main-bar-content div:after {
    margin: 2rem 25% 2rem 25%;
  }

  body #main-bar-logo {
    order: 1;
    height: 8rem;
  }

  body .copyright {
    order: 2;
    margin-bottom: 1rem;
  }
}

body {
  background-color: black;
  color: white;
  font-family: "market_decoregular", sans-serif;
  z-index: 1;
  overflow: hidden;
  touch-action: none;
}

.enter {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  top: 20%;
}

.enter img {
  height: 25rem;
}

.enter img:hover {
  -webkit-filter: drop-shadow(0px 0px 0px #800a12 );
  filter: drop-shadow(0px 0px 0px #800a12 );
}

h1 {
  font-family: "market_decoregular", sans-serif;
  padding-bottom: 15px;
  text-transform: uppercase;
  font-size: 18px;
}

.zizi-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  background-color: #2e010f;
  background: linear-gradient(0deg, rgba(7,0,2,1) 2%, rgba(46,1,15,1) 70%, rgba(46,1,15,1) 100%);
  color: #ff9c84;
  transition: left 1s ease;
}

.zizi-sidebar.closed {
  left: -27rem;
}

.zizi-sidebar a:hover {
  color: #b22d3f;
}

#main-bar-logo {
  -webkit-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

.button-sidebar {
  box-shadow: 0px 0px 41px 10px rgba(0, 0, 0, 0.7);
  background-color: #380011;
  padding: 1rem;
}

.mini-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.button-sidebar .zizi-icon {
  font-size: 2.1rem;
}

.mini-sidebar .centered-buttons, .mini-sidebar .top-buttons {
  display: flex;
  flex-direction: column;
}

.mini-sidebar .centered-buttons > a {
  padding: 20px 0 20px 0;
}


.close-sidebar-left {
  box-shadow: 0px 0px 41px 10px rgba(0, 0, 0, 0.7);
  background-color: #380011;
  width: 15px;
  font-size: 2.1rem;
  z-index: 1;
}

.close-button2 {
  font-size: 2.1rem;
  padding: 15px;
  padding-left: 30px;
  width: 30px;
  height: 30px;
  display: inline-flex;
}

.main-sidebar {
  width: 27rem;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  overflow: scroll;
}

.main-sidebar .zizi-icon {
  font-size: 2rem;
}

.main-sidebar a.inline-link {
  text-decoration: none;
  color: #ffbcaf;
}
.main-sidebar a.inline-link:hover {
  text-decoration: none;
  color: #d63a00;
}

.main-bar-content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.main-bar-content > div {
  padding: 1rem;
}

.main-bar-content .player-controls a {
  margin: 0 1rem 0 1rem;
}

.main-bar-content div:after {
  content: "";
  width: 50%;
  height: 1px;
  background-color: #ffbcaf;
  display: block;
  margin: 3rem 25% 3rem 25%;
}

.copyright{
  margin-bottom: 5rem;
}

.main-sidebar sub {
  display: block;
}

.main-sidebar .now-playing p {
  margin: 0;
  font-size: 1.1rem;
}

.zizi-sidebar .main-bar-content .sidebar-large-button {
  font-size: 2rem;
}

.secondary-sidebar {
  position: absolute;
  top: 0;
  right: 0;
  width: 25rem;
  height: 100%;
  display: flex;
  background-color: #2e010f;
  background: linear-gradient(0deg, rgba(7,0,2,1) 2%, rgba(46,1,15,1) 70%, rgba(46,1,15,1) 100%);
  color: #ff9c84;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  transition: right 1s ease;
}

.secondary-sidebar.closed {
  right: -27rem;
}

.secondary-sidebar .content-sidebar {
  overflow: scroll;
  padding-top: 40px;
  width: 100%;
}

.performers-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  line-height: 0.2rem;
}

.performers-list .performer {
  padding-bottom: 0.8rem;
}

.performers-list a:hover {
  box-shadow: 0px 0px 50px 4px rgba(0,0,0,1);
}

.secondary-header {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  text-align: center;
}


.secondary-sidebar .performer img {
  height: 10rem;
  box-shadow: 8px 8px 30px 0px rgba(0,0,0,0.75);
}

.secondary-sidebar .song img {
  height: 4rem;
  float: left;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}

.secondary-sidebar .song {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  text-align: left;
  margin-bottom: 1rem;
  /* background-color: #38001170; */
}

.secondary-sidebar .song:hover {
  box-shadow: 0px 0px 50px 5px rgba(0,0,0,0.6);
}

.secondary-sidebar .song p {
  height: 4rem;
  font-size: 0.85rem;
  padding-left: 1rem;
  width: 200px;
  display: table-cell;
  vertical-align: middle;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.secondary-sidebar .song w {
  color: #fff;
}
.secondary-sidebar .song:hover w {
  color: #d63a00;
}
.secondary-sidebar .song a:hover {
  color: #ff9c84;
}
.secondary-sidebar .song .artist {
  color: #edb49f;
}
.secondary-sidebar .song .artist:hover {
  color: #d63a00;
}

.secondary-sidebar .about {
  font-family: 'Lato';
  height: 5rem;
  float: left;
  text-align: left;
  font-size: 15px;
  font-size: 0.85rem;
}

.secondary-sidebar .about p {
  margin: 1.5rem;
}

.curtain-bg {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transform: translate(-50%, -50%);
  z-index: -4;
}

.curtain-side {
  position: absolute;
  height: 101%;
  left: 50%;
  top: 50%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transform: translate(-50%, -50%);
  /* mix-blend-mode: overlay; */
  z-index: -1;
}

.feather-mask {
  position: absolute;
  max-height: 100%;
  height: 100%;
  /* width: 600px; */
  /* inherit width/height from player */
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: -1;
  box-shadow: 150px 0px 100px -50px black inset,
    -150px 0px 100px -50px black inset;
}

.primary-player {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;

  mix-blend-mode: lighten;
  z-index: -2;
}

.shadow-player {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;

  mix-blend-mode: multiply;
  z-index: -3;
}

.button-group {
  line-height: 2;
  font-size: 2vw;
}

/* unvisited link */
a:link {
  color: #d98b6f;
}

/* visited link */
a:visited {
  color: #edb49f;
}

/* mouse over link */
a:hover {
  color: #d63a00;
}

/* selected link */
a:active {
  color: #fff;
}

a:disabled {
  color: #555;
}

